@import url(https://db.onlinewebfonts.com/c/02cbb7a31cfe23290e89f1316ea8886f?family=October+Crow);
@import url(https://db.onlinewebfonts.com/c/800249dca51965ef8adbfa527c330d6f?family=Teen);
@import url(https://db.onlinewebfonts.com/c/02cbb7a31cfe23290e89f1316ea8886f?family=October+Crow);
@import url(https://db.onlinewebfonts.com/c/800249dca51965ef8adbfa527c330d6f?family=Teen);
.header {
  font-size: 18px;
  line-height: 167%;
  color: #e4f357;
  background: transparent;
  position: relative;
  z-index: 10;
}
.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__menu-list-wrapper ul {
  display: flex;
  gap: 72px;
  list-style: none;
}
.header__button {
  font-family: "Teen", sans-serif;
  font-weight: 700;
  font-size: 16px;
  color: #1f261d;
  border-radius: 4px;
  padding: 10px 32px;
  margin-left: 52px;
  width: 115px;
  height: 40px;
  background: linear-gradient(180deg, #e4f357 0%, #a6b22e 100%);
}

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

a {
  text-decoration: none;
  font: inherit;
  color: inherit;
  cursor: pointer;
}

body {
  color: red;
  font-family: "Teen", sans-serif;
  font-weight: 400;
}

.container {
  max-width: 1170px;
  margin: 0 auto;
}

.hero-background-wrapper {
  background: linear-gradient(315deg, #223114 0%, #6a9a3c 13.74%, #537433 29.32%, #1e2b13 55.21%, #000 100%);
  padding-top: 36.5px;
  position: relative;
}
.hero-background-wrapper__background-img {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 1;
}

.hero-section {
  position: relative;
  z-index: 2;
}
.hero-section__content {
  width: 700px;
  margin-top: 241.5px;
  padding-bottom: 325px;
}
.hero-section__text {
  text-transform: uppercase;
  font-family: "October Crow", sans-serif;
  font-weight: 400;
  font-size: 102px;
  line-height: 98%;
  background: linear-gradient(360deg, #a0eb00 0%, #e4f357 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  margin-bottom: 60px;
}
.hero-section__buttons__calculator {
  font-family: "Teen", sans-serif;
  font-weight: 700;
  font-size: 18px;
  color: #1f261d;
  border: none;
  border-radius: 4px;
  padding: 24px 32px;
  width: 203px;
  height: 70px;
  background: linear-gradient(180deg, #e4f357 0%, #a6b22e 100%);
  margin-right: 30px;
}
.hero-section__buttons__read {
  font-family: "Teen", sans-serif;
  font-weight: 700;
  font-size: 18px;
  background: linear-gradient(180deg, #e4f357 0%, #a6b22e 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  border: 1px solid #e4f357;
  border-radius: 4px;
  padding: 24px 32px;
  width: 158px;
  height: 70px;
}